import React from "react"
import { Link as GatsbyLink} from "gatsby"
import PropTypes from "prop-types"

const Link = ({ context, children }) => (
  <>
    <GatsbyLink to={context.src}>{context.title}{children}</GatsbyLink>
  </>
)

Link.defaultProps = {
  context: {}
}

Link.propTypes = {
  context: PropTypes.object
}

export default Link
