/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"

import SEO from "../components/utils/seo"
import Debug from "../components/utils/debug"

import Header from "../components/nav/header"
import Footer from "../components/nav/footer"
import Link from "../components/link"
// import "./layout.css"

const BaseTemplate = ({ pageContext, children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery2 {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  const ComponentMap = {
    "Link": Link,
  };

  console.log(pageContext);

  function generateComponents(componentArr) {
    return componentArr.map((component, index) => {
      const DynamicComponent = ComponentMap[component.type];
      if (DynamicComponent) {
        if (component.components) { 
          return (
            <DynamicComponent key={index} {...component}>
              {generateComponents(component.components)}
            </DynamicComponent>
          )
        } else {
          return <DynamicComponent key={index} {...component} />
        }
      }
      return null;
    })
  }

  const dc = generateComponents(pageContext.components);
  console.log(dc);

  return (
    <>
      <Debug>{JSON.stringify(pageContext)}</Debug>
      <SEO title={pageContext.title} description={pageContext.description} />
      <Header siteTitle={data.site.siteMetadata?.title || `Title`} />
      <div>
        <main>{pageContext.title}</main>

        {/* Dynamically generate page components from src/data/pages and pass context */}
        { pageContext.components && dc}

        {/* Render children when used with non-dynamically generated pages (src/pages) */}
        {children}

      </div>
      <Footer />
    </>
  )
}

BaseTemplate.propTypes = {
  pageContext: PropTypes.object.isRequired,
}

BaseTemplate.defaultProps = {
  pageContext: {},
}

export default BaseTemplate
